.tab-page {

	.tabs {
		border-bottom: 1px solid $gray-light;
		margin-bottom: 1rem;
		height: 50px;
		text-align: center;

		@media (min-width: 544px) {
			margin-bottom: 3rem;
		}

		a {
			color: $gray;
			display: inline-block;
			cursor: pointer;
			height: 50px;
			line-height: 48px;
			border-bottom: 2px solid transparent;
			overflow: hidden;
			transition: all .2s ease-out;
			font-size: .875rem;

			@media (min-width: 544px) {
				font-size: 1rem;
			}

			&.active {
				cursor: default;
			}

			&:hover,
			&.active {
				border-bottom-color: $asphalt;
				color: $asphalt;
			}
		}
	}
}



