.account-login {

	.btn,
	.form-control {
		border-radius: 0;
		font-size: .875rem;
	}

	.btn {
		background-color: $asphalt;
		border-color: $asphalt;

		&:hover,
		&:focus {
			background-color: $asphalt-dark;
			border-color: $asphalt-dark;
		}
	}

	.form-control {
		color: $asphalt;
		border-color: $gray-light;

		&:focus {
			border-color: $asphalt;
		}
	}

	.message {
		margin-bottom: 0;
		overflow: hidden;
		font-size: .75rem;
		height: 2rem;

		&-enter-active,
		&-leave-active {
			transition: height .2s ease-out;
		}

		&-enter,
		&-leave-active {
			height: 0;
		}
	}
}



